import React from 'react';
import { IconBaseProps, IconType } from 'react-icons';

interface IconProps {
  icon: IconType;
  size?: number | string;
  className?: string;
  color?: string;
  [x: string]: any; // Allow any other props to be passed through
}

const Icon: React.FC<IconProps> = ({ icon: IconComponent, size = 20, className = '', ...props }) => {
  return React.createElement(IconComponent as React.ComponentType<any>, {
    size,
    className,
    ...props
  });
};

export default Icon; 